@charset "UTF-8";

// Variables
$root-font-size: 16px !default;
/// Remove the unit of a length
///
/// @example
///   strip-unit(32px)
///
/// @output
///   32
///
/// @param {Number} $number - Number to remove unit from
/// @return {Number} - Unitless number
///
@function strip-unit($number) {
  @if (type-of($number)=='number' and not unitless($number)) {
    @return $number / ($number * 0 + 1);
  }

  @return $number;
}

/// Convert pixel values to rem values
///
/// @example
///   rem(32px, 16px);
///
/// @output
///   2rem;
///
/// @param {number} $pxValue
/// @param {number} $context
///
@function rem($pxValue, $context: $root-font-size) {
  @if (not unitless($pxValue)) {
    $pxValue: strip-unit($pxValue);
  }

  @if (not unitless($context)) {
    $context: strip-unit($context);
  }

  @return $pxValue/$context * 1rem;
}

/// Convert pixel values to em values
///
/// @example
///   em(32px, 16px);
///
/// @output
///   2em;
///
/// @param {number} $pxValue
/// @param {number} $context
///
@function em($pxValue, $context: $root-font-size) {
  @if (not unitless($pxValue)) {
    $pxValue: strip-unit($pxValue);
  }

  @if (not unitless($context)) {
    $context: strip-unit($context);
  }

  @return $pxValue/$context * 1em;
}



