@charset "UTF-8";

@import "framework/utils/functions/_unit-conversion";
@import "framework/utils/variables/_colors";

.dpl {
  font-family: nta, Arial, sans-serif;

  button,
  input,
  select,
  textarea {
    font-family: nta, Arial, sans-serif;
  }
}

// Page Header
.dpl-header {
  background: $black;
  color: $white;
  padding: rem(15) 0 rem(5) 0;
  margin-bottom: rem(30);
}

// Page footer
.dpl-footer {
  background: $grey-3;
  color: black;
  padding: rem(15) 0;
}

// Main
.dpl-main {
  padding-top: 30px;
}

.dpl-section {
  margin-bottom: 30px;
}

.dpl-example {
  background: $white;
  border: 1px solid $grey-2;
  padding: 45px 15px 15px 15px;
  margin-top: 15px;
  margin-bottom: 60px;
  position: relative;

  &::before {
    content: "Example";
    position: absolute;
    right: 0;
    top: 0;
    padding: 0.25em 0.75em;
    font-size: 14px;
    line-height: 1.25;
    font-weight: 400;
    text-transform: none;
    background: $govuk-blue;
    color: white;
  }
}

.dpl-grid-example {
  background: rgba(0, 96, 255, 0.1);
  border: 1px solid rgba(0, 96, 255, 0.2);
  padding-top: 16px;
  text-align: center;
}

.dpl-anchor-list {
  margin-bottom: rem(30);
  border: 1px solid $border-color;
  background: $grey-4;
  padding: rem(15) rem(15) rem(10) rem(15);
  max-width: 15em;
}



